import { Address, IAddress } from 'domain/value-objects/address/address';
import { IValueObject } from '../base/index';

export interface IMaterialsDemand extends IValueObject {
    charityBoxesAmount?: number | undefined;
    postersAmount?: number | undefined;
    acknowledgementsAmount?: number | undefined;
    deliveryAddress: IAddress;
    copyAddressFromStaffAddress: boolean;
}

export class MaterialsDemand implements IMaterialsDemand {
  charityBoxesAmount?: number;

  postersAmount?: number;

  acknowledgementsAmount?: number;

  deliveryAddress: IAddress;

  copyAddressFromStaffAddress: boolean;

  /**
     *
     */
  constructor(materialsDemand: IMaterialsDemand) {
    this.charityBoxesAmount = materialsDemand.charityBoxesAmount;
    this.postersAmount = materialsDemand.postersAmount;
    this.acknowledgementsAmount = materialsDemand.acknowledgementsAmount;
    this.deliveryAddress = new Address(materialsDemand.deliveryAddress);
    this.copyAddressFromStaffAddress = materialsDemand.copyAddressFromStaffAddress;
  }
}
