import { IEntity, IAggregateRoot } from 'domain/base';
import { FullName } from 'domain/value-objects';

import { UserPermissions } from './user-permissions';

export interface IUser extends IEntity, IAggregateRoot {
    userId: string;
    fullName: FullName;
    emailAddresses: Array<string>;
    permissions: UserPermissions;
}

export class User implements IUser {
  userId: string;

  fullName: FullName;

  emailAddresses: string[];

  permissions: UserPermissions;

  constructor(user: IUser) {
    this.userId = user.userId;
    this.fullName = new FullName(user.fullName);
    this.emailAddresses = user.emailAddresses;
    this.permissions = new UserPermissions(user.permissions);
  }
}
