import {
  FullName, IFullName, Address, IAddress,
} from 'domain/value-objects';
import { IValueObject } from '../base/index';

export interface IPlenipotentiary extends IValueObject {
    fullName: IFullName,
    pesel: string,
    emailAddress: string;
    phoneNumber: string;
    address: IAddress,
    gdprConsent: boolean,
}

export class Plenipotentiary implements IPlenipotentiary {
  fullName: IFullName;

  pesel: string;

  emailAddress: string;

  phoneNumber: string;

  address: IAddress;

  gdprConsent: boolean;

  /**
     *
     */
  constructor(plenipotentiary: IPlenipotentiary) {
    this.fullName = new FullName(plenipotentiary.fullName);
    this.pesel = plenipotentiary.pesel;
    this.emailAddress = plenipotentiary.emailAddress;
    this.phoneNumber = plenipotentiary.phoneNumber;
    this.address = new Address(plenipotentiary.address);
    this.gdprConsent = plenipotentiary.gdprConsent;
  }
}
