import { User } from 'domain/identity/user';
import { UserRepository } from 'infrastructure/domain/user/user-repository';
import { UserStore } from 'domain/identity/user-store';
import React, {
  lazy, useContext, useEffect,
} from 'react';
import { useMutation } from 'react-query';
import { Routes, Route } from 'react-router';
import { observer } from 'mobx-react';
import { PageLoaderIcon } from 'ui/components/atoms';
import { DefaultLayout, EmptyLayout } from 'ui/components/layouts';
import { OnlyAuthenticatedLayout } from 'ui/components/layouts/only-authenticated-inner-layout/only-authenticated-inner-layout';

const AuthenticatedHomePage = lazy(
  () => import('ui/components/pages').then((module) => ({ default: module.AuthenticatedHomePage })),
);

const AuthenticatedDpRoutes: React.FC = (): JSX.Element => {
  const userStore = useContext(UserStore);

  const mutation = useMutation<User>(() => UserRepository.getUser());

  useEffect(() => {
    mutation.mutate();
  }, []);

  useEffect(() => {
    if (mutation.isSuccess) {
      userStore.update(mutation.data);
    }
  }, [mutation.isSuccess]);

  return !userStore.user ? (<EmptyLayout><PageLoaderIcon /></EmptyLayout>) : (
    <Routes>
      <Route path="/" element={<DefaultLayout />}>
        <Route path="/" element={<OnlyAuthenticatedLayout />}>
          <Route index element={<AuthenticatedHomePage />} />
          <Route
            path="*"
            element={<AuthenticatedHomePage />}
          />
        </Route>
      </Route>
    </Routes>
  );
};

export default observer(AuthenticatedDpRoutes);
