import { IValueObject } from '../base/index';

export interface IClaim extends IValueObject {
    type: string,
    value: ClaimNames,
}

export class Claim implements IClaim {
  type: string;

  value: ClaimNames;

  constructor(claim: IClaim) {
    this.type = claim.type;
    this.value = claim.value;
  }
}

export enum ClaimNames {
    CanSearchStaffs = 'CanSearchStaffs',
    CanDownloadStaffsListReport = 'CanDownloadStaffsListReport'
}
