import { ErrorsHandler } from 'infrastructure/errors-handling';
import { HttpClient, httpClient } from 'infrastructure/http-client';
import { IValidationService } from 'domain/shared-abstraction';

class ValidationService implements IValidationService {
  private readonly httpClient: HttpClient;

  private readonly endpoint: string;

  constructor(httpClient: HttpClient, endpoint: string) {
    this.httpClient = httpClient;
    this.endpoint = endpoint;
  }

  isPeselValid = async (pesel: string): Promise<boolean> => {
    try {
      const result = await this.httpClient.get<void, boolean>({ url: `${this.endpoint}/pesel/${pesel}` });

      return Promise.resolve(result);
    } catch (error) {
      ErrorsHandler.handle(error);

      return Promise.reject(error);
    }
  };
}

const validationService = new ValidationService(httpClient, '/Validation');

export { validationService as ValidationService };
