import React, { useContext } from 'react';
import { observer } from 'mobx-react';
import { AuthStore, useLogOutEffect } from 'infrastructure/authentication';
import { AuthenticationStatus } from 'infrastructure/authentication/auth-store';
import AnonymousDpRoutes from './anonymous-dp-routes';
import AuthenticatedDpRoutes from './authenticated-dp-routes';

const DpRoutes: React.FC = (): JSX.Element => {
  const authStore = useContext(AuthStore);

  useLogOutEffect(() => {
    authStore.updateAuthenticationStatus(AuthenticationStatus.NotAuthenticated);
  });

  return !authStore.isAuthenticated ? <AnonymousDpRoutes /> : <AuthenticatedDpRoutes />;
};

export default observer(DpRoutes);
