import clsx from 'clsx';
import React, { ReactNode } from 'react';
import styled from 'styled-components';

export interface LabelInputProps {
  id: string,
  label: string,
  children?: ReactNode,
  bottomlabel?: ReactNode,
  className?: string,
}

const Span = styled.span`
  font-size: small;

  input:-webkit-autofill ~ label {
    top: -.75rem;
  }

`;

const LabelInput = (props: LabelInputProps) => {
  const {
    id, label, children, bottomlabel, className,
  } = props;

  return (
    <div className="field">
      <Span className={clsx('p-float-label', className)}>
        {children}
        <label htmlFor={id}>
          {label}
        </label>
      </Span>
      {bottomlabel}
    </div>
  );
};

export { LabelInput };
