/* eslint-disable import/no-unresolved */
/* eslint-disable max-len */
/* eslint-disable @typescript-eslint/no-unused-vars */
import { RoleNames } from 'domain/identity/role';
import { IUser } from 'domain/identity/user';
import { MenuItem } from 'primereact/menuitem';
import {
  useState, useContext, useCallback, useMemo,
} from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router';
import { iconsMap } from 'ui/utils';
import { AuthenticationService, AuthStore } from 'infrastructure/authentication';
import { AuthenticationStatus } from 'infrastructure/authentication/auth-store';

const useMenu = (initialIsAuthenticated: boolean, initialUser?: IUser): [MenuItem[], () => void, (user: IUser) => void] => {
  const [isAuthenticated, setIsAuthenticated] = useState(initialIsAuthenticated);
  const [user, setUser] = useState(initialUser);

  const { t } = useTranslation();
  const navigate = useNavigate();
  const authStore = useContext(AuthStore);

  const result = useMemo(
    () => (isAuthenticated ? [
      {
        icon: `pi font-bold pi-fw ${iconsMap.homePage}`,
        label: t('papalDay'),
        expanded: false,
        command: () => navigate('/'),
      },
      {
        separator: true,
      },
      {
        icon: `pi font-bold pi-fw ${iconsMap.logOut}`,
        label: t('logOut'),
        expanded: false,
        command: () => {
          AuthenticationService.logout();
          authStore.updateAuthenticationStatus(
            AuthenticationStatus.NotAuthenticated,
          );
        },
      },
    ] : [
      {
        icon: `pi font-bold pi-fw ${iconsMap.homePage}`,
        label: t('papalDay'),
        expanded: false,
        command: () => navigate('/'),
      },
      {
        separator: true,
      },
      {
        icon: `pi font-bold pi-fw ${iconsMap.staff}`,
        label: t('registerStaff'),
        expanded: false,
        command: () => navigate('/rejestracja'),
      },
      {
        separator: true,
      },
      {
        icon: `pi font-bold pi-fw ${iconsMap.user}`,
        label: t('logIn'),
        expanded: false,
        command: () => navigate('/'),
      },
    ]),
    [isAuthenticated],
  );

  const logout = useCallback(() => {
    setIsAuthenticated(false);
    setUser(undefined);
  }, []);

  const logIn = useCallback((user: IUser) => {
    setIsAuthenticated(true);
    setUser(user);
  }, []);

  return [result, logout, logIn];
};

export { useMenu };
