const isValidPeselLocal = (pesel: string) => {
  if (pesel) {
    const weights = [1, 3, 7, 9, 1, 3, 7, 9, 1, 3];
    const controlNumber = parseInt(pesel[10], 10);

    const sum = weights.reduce((prev, current, index) => (
      prev + (parseInt(pesel.substring(index, index + 1), 10) * current)
    ), 0);
    return sum > 0 && (10 - (sum % 10)) % 10 === controlNumber;
  }
  return false;
};

export { isValidPeselLocal };
