import { IValueObject } from '../../base/index';

export interface IFullName extends IValueObject {
  firstName: string,
  lastName: string,
}

export class FullName implements IFullName {
  firstName: string;

  lastName: string;

  get name(): string {
    return `${this.firstName} ${this.lastName}`;
  }

  constructor(fullName: IFullName) {
    this.firstName = fullName.firstName;
    this.lastName = fullName.lastName;
  }
}
