/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable import/no-unresolved */
import React, { useContext, useEffect, useMemo } from 'react';
import styled from 'styled-components';
import logo from 'ui/img/logo.png';
import { deviceSize } from 'ui/utils';
import { AuthStore } from 'infrastructure/authentication';
import { UserStore } from 'domain/identity/user-store';
import { observer } from 'mobx-react';
import { Menubar } from '../../atoms/menubar/menubar';
import NavbarEndMenuItems from './navbar-end-menu-items';
import { useMenu } from './use-menu';

const backgroundNavbar = 'rgba(255, 255, 255)';

const StyledMenubar = styled(Menubar)`
  border: 0;
  border-radius: 0;
  height: 4rem;
  padding-top: 0;
  padding-bottom: 0;
  background-color: ${backgroundNavbar};
  .p-menubar-root-list {
    margin-right: auto;
    box-shadow: none;
  }
  .p-menubar-root-list, .p-menuitem, .p-menuitem-link {
    height: 100%;
  }
  .p-menuitem {
    min-width: 12.5rem;
    background-color: ${backgroundNavbar};
  }
  .p-menubar-root-list > .p-menuitem > .p-menuitem-link {
    border-radius: 0;
    justify-content: center;
    @media screen and (max-width: ${deviceSize.tablet}px) {
      justify-content: left;
    }
  }
  .p-menubar-root-list > .p-menu-separator {
    border-top: 0;
    width: 1px;
    height: 60%;
    background: #0000004d;
    @media screen and (max-width: ${deviceSize.tablet}px) {
      margin: 0;
      width: 0;
      height: 0;
      background: ${backgroundNavbar};
    }
  }
  .p-menubar-start {
    z-index: 1002;
  }
  .p-menubar-button {
    width: 4rem;
    height: 4rem;
  }
`;

const Start = styled.a`
    img {
      height: 6rem;
      margin-top: 3rem;
      max-width: 6rem;
      @media screen and (max-width: ${deviceSize.tablet}px) {
        height: 4rem;
        margin-top: unset;
        max-width: 4rem;
      }
    }
`;

const Navbar = () => {
  const start = useMemo(() => (
    <Start className="mr-2" href="/">
      <img alt="logo" src={logo} />
    </Start>
  ), []);

  const authStore = useContext(AuthStore);
  const userStore = useContext(UserStore);

  const [menuItems, logOut, logIn] = useMenu(authStore.isAuthenticated, userStore.user);

  useEffect(() => {
    if (authStore.isAuthenticated) {
      logIn(userStore.user!);
    } else {
      logOut();
    }
  }, [authStore.isAuthenticated]);

  return (
    <StyledMenubar className="relative z-1 shadow-6 justify-content-between lg:justify-content-start" start={start} end={<NavbarEndMenuItems />} model={menuItems} />
  );
};

const ObservedNavbar = observer(Navbar);

export { ObservedNavbar as Navbar };
