/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable max-len */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/jsx-props-no-spreading */
import React, {
  useContext, useEffect, useRef, useMemo, useCallback,
} from 'react';
import { FormProvider, useForm, Validate } from 'react-hook-form';
import {
  FormCheckbox, FormInputMask, FormInputText,
} from 'ui/components/molecues';
import { useTranslation } from 'react-i18next';
import {
  AnimatedDiv, AppIcon, Divider, ErrorMessage,
} from 'ui/components/atoms';
import { observer } from 'mobx-react';
import { Plenipotentiary } from 'domain/staff/plenipotentiary';
import { FullNameValidationRules } from 'domain/value-objects/full-name/full-name-rules';
import { createPlenipotentiaryValidationRules } from 'domain/staff';
import { ValidationService } from 'infrastructure/services';
import { AddressRegistrationFields } from '../../address-registration-fields/address-registration-fields';
import { RegisterFormStore } from '../register-form-store';
import { FormStepProps } from '../form-step-props';

const PlenipotentiaryValidationRules = createPlenipotentiaryValidationRules(ValidationService);

// eslint-disable-next-line max-len
const PlenipotentiaryRegistrationForm = (props: FormStepProps) => {
  const store = useContext(RegisterFormStore);

  const plenipotentiaryRegistrationForm = useForm<Plenipotentiary>({
    defaultValues: store.plenipotentiary,
    reValidateMode: 'onChange',
  });

  const {
    control, getValues, formState, handleSubmit,
  } = plenipotentiaryRegistrationForm;

  const form = useRef<HTMLFormElement | null>(null);

  useEffect(() => {
    store.isCurrentStepValid = formState.isValid;
  }, [formState.isValid]);

  useEffect(() => {
    if (store.validateCurrentStep === true) {
      form.current!.dispatchEvent(new Event('submit', { bubbles: true, cancelable: true }));
    }
  }, [store.validateCurrentStep, store.validateTimeStamp]);

  const sendForm = useCallback((e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    handleSubmit(() => {
      store.plenipotentiary = getValues();
      store.isCurrentStepValid = undefined;
      store.validateCurrentStep = undefined;
      store.validateTimeStamp = undefined;
      props.onSubmit();
    })(e);
  }, []);

  useEffect(() => () => {
    store.plenipotentiary = getValues();
  }, []);

  const { t } = useTranslation();

  const errors = useMemo(() => formState.errors, [formState]);

  return (
    <AnimatedDiv>
      <FormProvider {...plenipotentiaryRegistrationForm}>
        <form ref={form} onSubmit={sendForm} className="formgrid grid">
          <Divider align="center">
            <AppIcon usage="plenipotentiary" className="pr-2" />
            {t('plenipotentiaryInfo')}
          </Divider>
          <div className="field col-12 md:col-6">
            <FormInputText
              id="fullName.firstName"
              control={control}
              label={t('firstName')}
              rules={FullNameValidationRules.firstName as Record<string, Validate<string>>}
              name="fullName.firstName"
              defaultValue=""
            />
          </div>
          <div className="field col-12 md:col-6">
            <FormInputText
              id="fullName.lastName"
              control={control}
              name="fullName.lastName"
              label={t('lastName')}
              rules={FullNameValidationRules.lastName as Record<string, Validate<string>>}
              defaultValue=""
            />
          </div>
          <div className="field col-12 md:col-3">
            <FormInputMask
              id="pesel"
              control={control}
              name="pesel"
              label={t('peselNumber')}
              mask="99999999999"
              autoClear={false}
              rules={PlenipotentiaryValidationRules.pesel as Record<string, Validate<string>>}
              defaultValue=""
            />
          </div>
          <div className="field col-12 md:col-3">
            <FormInputText
              id="phoneNumber"
              control={control}
              name="phoneNumber"
              label={t('phoneNumber')}
              rules={PlenipotentiaryValidationRules.phoneNumber as Record<string, Validate<string>>}
              defaultValue=""
            />
          </div>
          <div className="field col-12 md:col-6">
            <FormInputText
              id="emailAddress"
              control={control}
              name="emailAddress"
              label={t('emailAddress')}
              rules={PlenipotentiaryValidationRules.emailAddress as Record<string, Validate<string>>}
              defaultValue=""
            />
          </div>
          <Divider align="center">
            <AppIcon usage="address" className="pr-2" />
            {t('address')}
          </Divider>
          <AddressRegistrationFields name="address" />
          <div className="field col-12 md:col-12">
            {errors.gdprConsent && <ErrorMessage message={t(errors.gdprConsent!.message! as any)} />}
            <FormCheckbox
              inputId="gdprConsent"
              control={control}
              name="gdprConsent"
              label={t('gdprConsent')}
              defaultChecked={false}
              rules={PlenipotentiaryValidationRules.gdprConsent as Record<string, Validate<string>>}
              className="align-items-start"
            />
          </div>
        </form>
      </FormProvider>
    </AnimatedDiv>
  );
};

const ObservedPlenipotentiaryRegistrationForm = observer(PlenipotentiaryRegistrationForm);

export { ObservedPlenipotentiaryRegistrationForm as PlenipotentiaryRegistrationForm };
