/* eslint-disable max-len */
import { ClaimNames } from 'domain/identity/claim';
import { UserStore } from 'domain/identity/user-store';
import { BusinessValidationExceptionResultModel } from 'infrastructure/api-models/business-validation-exception-result-model';
import { staffRepository } from 'infrastructure/domain/staff';
import React, { useContext } from 'react';
import { useMutation } from 'react-query';
import { ButtonSuccess } from 'ui/components/atoms';
import { iconsMap } from 'ui/utils';
import { observer } from 'mobx-react';
import { useTranslation } from 'react-i18next';

const AuthenticatedHomePage = () => {
  const userStore = useContext(UserStore);

  const { t } = useTranslation();

  const mutation = useMutation<unknown, BusinessValidationExceptionResultModel>(() => staffRepository.getStaffsReport());

  const downloadStaffsReport = () => mutation.mutate();

  return (
    <div className="ml-auto mt-auto mb-auto mr-auto h-full flex flex-column justify-content-center align-items-center">
      <div className="text-center">
        <strong className="text-4xl">
          { `${t('hello')} ${userStore.user!.fullName!.name}` }
        </strong>
      </div>
      <div className="p-4">
        {userStore.user!.permissions.hasClaim(ClaimNames.CanDownloadStaffsListReport) && <ButtonSuccess loading={mutation.isLoading} label={t('staffsReportButtonLabel')} icon={`pi ${iconsMap.excel}`} onClick={downloadStaffsReport} />}
      </div>
    </div>
  );
};

const ObservedAuthenticatedHomePage = observer(AuthenticatedHomePage);

export { ObservedAuthenticatedHomePage as AuthenticatedHomePage };
