import React, { ReactNode } from 'react';
import {
  useController, UseControllerProps,
} from 'react-hook-form';
import { ErrorMessage } from 'ui/components/atoms';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';

type FormInputProps<ChildPropsType, FormInputType> = ChildPropsType &
  UseControllerProps<FormInputType> & {
    bottomlabel?: ReactNode,
    isCheckBox?: boolean,
    ChildComponent: (props: ChildPropsType) => JSX.Element
  }

const FormInput = <ChildPropsType, FormInputType>(
  props: FormInputProps<ChildPropsType, FormInputType>,
) => {
  const {
    name,
    rules,
    shouldUnregister,
    control,
    defaultValue,
    isCheckBox,
    ChildComponent,
    ...childProps
  } = props;

  const { field } = useController({
    name, rules, shouldUnregister, control, defaultValue,
  });

  const { t } = useTranslation();

  const {
    onBlur, onChange, name: fieldName, value,
  } = field;

  return (
    <ChildComponent
      name={fieldName}
      value={isCheckBox ? undefined : value}
      checked={isCheckBox ? value : undefined}
      onChange={onChange}
      onBlur={onBlur}
      className={clsx({ 'p-invalid': control?.getFieldState(fieldName).error })}
      bottomlabel={control?.getFieldState(fieldName).error?.message && (
        <ErrorMessage
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          message={`${t(control?.getFieldState(fieldName).error?.message as any)}`}
        />
      )}
      // eslint-disable-next-line react/jsx-props-no-spreading
      {...childProps as Extract<typeof childProps, ChildPropsType>}
    />
  );
};

export { FormInput };
