import React, { useCallback, useState } from 'react';
import { ButtonSecondary, Dialog } from 'ui/components/atoms';
import Logger from 'infrastructure/logging/logger';
import { useTranslation } from 'react-i18next';

interface ErrorFallbackProps {
  error: Error,
  resetErrorBoundary: () => void;
}

const ErrorFallback = (props: ErrorFallbackProps) => {
  const { error, resetErrorBoundary } = props;
  const [isVisible, setIsVisible] = useState(true);
  const { t } = useTranslation();

  const onHide = useCallback(() => {
    setIsVisible(false);
  }, []);

  Logger.error(error);

  return (
    <Dialog header={`${t('somethingWentWrong')} 😥`} visible={isVisible} closable={false} onHide={onHide}>
      <div className="p-4">
        <ButtonSecondary type="button" onClick={resetErrorBoundary}>{t('tryAgain')}</ButtonSecondary>
      </div>
    </Dialog>
  );
};

export { ErrorFallback };
