/* eslint-disable operator-linebreak */
import { isValidPeselLocal, maximumLengthIs, ValidationMessages } from 'domain/shared-utils';
import { RegisterOptions } from 'react-hook-form';
import { EmailRegexp } from 'shared/regexp/email-regexp';
import { IValidationService } from '../shared-abstraction';
import { Plenipotentiary } from './plenipotentiary';

const createPlenipotentiaryValidationRules = (
  validationService: IValidationService,
) => {
  const PlenipotentiaryValidationRules: {
    [index in keyof Plenipotentiary]?: RegisterOptions<Plenipotentiary, index>;
  } = {
    pesel: {
      validate: async (value) => {
        if (
          value === undefined ||
          value === null ||
          value.trim() === '' ||
          Number.isNaN(value as unknown as number) ||
          !isValidPeselLocal(value)
        ) {
          return ValidationMessages.incorrectPesel;
        }

        const result = await validationService.isPeselValid(value);
        return result === false ? ValidationMessages.incorrectPesel : undefined;
      },
    },
    emailAddress: {
      required: {
        value: true,
        message: ValidationMessages.canNotBeEmpty,
      },
      maxLength: {
        value: 256,
        message: maximumLengthIs(256),
      },
      pattern: {
        value: new RegExp(EmailRegexp),
        message: ValidationMessages.emailAddressHasIncorrectFormat,
      },
    },
    gdprConsent: {
      validate: (v) => (
        v === true ? undefined : ValidationMessages.yourConsentIsRequiredForProceeding
      ),
    },
    phoneNumber: {
      required: {
        value: true,
        message: ValidationMessages.canNotBeEmpty,
      },
      maxLength: {
        value: 16,
        message: maximumLengthIs(16),
      },
    },
  };

  return PlenipotentiaryValidationRules;
};

export { createPlenipotentiaryValidationRules };
