import Environments from 'infrastructure/configuration/environments';
import { env } from '../configuration/env';
import Logger, { LoggerOptions } from './logger';

export default {
  init: () => {
    const loggerOptions: LoggerOptions = {
      isClientSideLoggingEnabled: env.REACT_APP_ENVIRONMENT === Environments.Development,
    };

    if (env.REACT_APP_ENVIRONMENT === Environments.Production) {
      loggerOptions.serverSideLogging = {
        enabled: true,
        loggerName: 'jsLogger',
        url: `${env.REACT_APP_API_URL}/jsnlog.logger`,
      };
    }

    Logger.init(loggerOptions);
  },
};
