import React, { ReactNode, useCallback, useMemo } from 'react';
import styled from 'styled-components';
import {
  ButtonSecondary, ButtonSuccess, Dialog, PageLoaderIcon,
} from 'ui/components/atoms';
import { deviceSize, iconsMap } from 'ui/utils';

export enum ConfirmDialogStatuses {
  confirmation,
  loading,
  success
}
interface ConfirmDialogProps {
  isVisible: boolean;
  onAccepted: () => void;
  onRejected: () => void;
  confirmationContent: ReactNode;
  successContent: ReactNode;
  successFooter: ReactNode;
  header?: string;
  acceptButtonLabel: string;
  rejectButtonLabel: string;
  width?: string;
  status: ConfirmDialogStatuses;
}

interface StyledDialogProps {
  status: ConfirmDialogStatuses;
}

const StyledDialog = styled(Dialog)<StyledDialogProps>`
  .p-dialog-content {
    ${(props) => (props.status === ConfirmDialogStatuses.confirmation ? null : 'display: flex')} ;
    justify-content: center;
    align-items: center;
    height: 80vh;
  }
  .p-dialog-content, .p-dialog-header {
    @media screen and (max-width: ${deviceSize.tablet}px) {
      padding-left: 1rem;
      padding-right: 1rem;
    }
  }
  .p-dialog-footer {
    padding: 1rem;
    display: flex;
    justify-content: right;
    align-items: center;
    button {
      width: 100%;
    }
    @media screen and (max-width: ${deviceSize.tablet}px) {
      justify-content: center;
      padding-left: 1rem;
      padding-right: 1rem;
    }
  }
`;

const ConfirmDialog = (props: ConfirmDialogProps) => {
  const {
    header,
    isVisible,
    onAccepted,
    onRejected,
    confirmationContent,
    successContent,
    successFooter,
    acceptButtonLabel,
    rejectButtonLabel,
    width,
    status,
  } = props;

  const footer = useMemo(() => (
    <div className="grid col-12 lg:col-6 px-0 lg:px-2">
      <div className="col-6 pl-0">
        <ButtonSecondary label={rejectButtonLabel} icon={`pi ${iconsMap.reject}`} onClick={() => onRejected()} />
      </div>
      <div className="col-6 pr-0">
        <ButtonSuccess label={acceptButtonLabel} icon={`pi ${iconsMap.accept}`} onClick={() => onAccepted()} autoFocus />
      </div>
    </div>
  ), [rejectButtonLabel, acceptButtonLabel, onAccepted, onRejected]);

  const createContent = useCallback((currentStatus: ConfirmDialogStatuses) => {
    switch (currentStatus) {
      case ConfirmDialogStatuses.confirmation:
        return {
          header,
          content: confirmationContent,
          footer,
        };
      case ConfirmDialogStatuses.loading:
        return {
          header: null,
          content: <PageLoaderIcon />,
          footer: null,
        };
      case ConfirmDialogStatuses.success:
        return {
          header: null,
          content: (
            <div className="fadein animation-duration-1000 text-center justify-content-center">
              {successContent}
            </div>
          ),
          footer: successFooter,
        };
      default:
        return null;
    }
  }, [status]);

  return (
    <StyledDialog
      header={createContent(status)?.header}
      closable={false}
      visible={isVisible}
      onHide={() => onRejected()}
      breakpoints={{ [`${deviceSize.tablet}px`]: '80vw', [`${deviceSize.mobileM}px`]: '90vw' }}
      style={{ width: width || '50rem' }}
      draggable={false}
      footer={createContent(status)?.footer}
      status={status}
    >
      {createContent(status)?.content}
    </StyledDialog>
  );
};

export { ConfirmDialog };
