/* eslint-disable operator-linebreak */
import { RegisterOptions } from 'react-hook-form';
import { Credentials } from 'domain/value-objects';
import { EmailRegexp } from 'shared/regexp/email-regexp';
import { ValidationMessages } from 'domain/shared-utils';

const CredentialsRules: {
    [index in keyof Credentials]: RegisterOptions<Credentials, index>
} = {
  username: {
    required: {
      value: true,
      message: ValidationMessages.canNotBeEmpty,
    },
    pattern: {
      value: new RegExp(EmailRegexp),
      message: ValidationMessages.emailAddressHasIncorrectFormat,
    },
  },
  password: {
    required: {
      value: true,
      message: ValidationMessages.canNotBeEmpty,
    },
  },
};

export enum CredentialsRulesErrorsCodes {
  EmailAddressOrPasswordIsIncorrect = 'EmailAddressOrPasswordIsIncorrect'
}

export { CredentialsRules };
