/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable max-len */
/* eslint-disable jsx-a11y/label-has-associated-control */
import { Credentials } from 'domain/value-objects';
import { AuthenticationService, AuthStore } from 'infrastructure/authentication';
import React, { useContext, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { useMutation } from 'react-query';
import { AppIcon, ButtonSecondary, ErrorMessage } from 'ui/components/atoms';
import { FormInputGroup } from 'ui/components/molecues';
import { observer } from 'mobx-react';
import { AuthenticationStatus } from 'infrastructure/authentication/auth-store';
import { CredentialsRules, CredentialsRulesErrorsCodes } from 'domain/value-objects/credentials/credentials-rules';
import { BusinessValidationExceptionResultModel } from 'infrastructure/api-models/business-validation-exception-result-model';

const userIcon = <AppIcon usage="user" />;
const passwordIcon = <AppIcon usage="password" />;

const LoginForm = () => {
  const {
    control, getValues, handleSubmit, formState: { errors },
  } = useForm<Credentials>({
    reValidateMode: 'onChange',
  });
  const { t } = useTranslation();

  const userStore = useContext(AuthStore);

  const mutation = useMutation<unknown, BusinessValidationExceptionResultModel, Credentials>((credentials) => AuthenticationService.authenticate(credentials.username, credentials.password));

  const sendLoginForm = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    handleSubmit(() => {
      mutation.mutate(getValues());
    })(e);
  };

  useEffect(() => {
    if (mutation.isSuccess) {
      userStore.updateAuthenticationStatus(AuthenticationStatus.Authenticated);
    }
  }, [mutation.isSuccess]);

  return (
    <form onSubmit={sendLoginForm} className="grid">
      <div className="col-12 md:col-12">
        <FormInputGroup
          control={control}
          placeholder={t('username')}
          start={userIcon}
          defaultValue=""
          rules={CredentialsRules.username}
          name="username"
        />
        {errors.username && <ErrorMessage message={t(errors.username!.message! as any)} />}
      </div>
      <div className="col-12 md:col-12">
        <FormInputGroup
          id="password"
          control={control}
          placeholder={t('password')}
          start={passwordIcon}
          defaultValue=""
          type="password"
          rules={CredentialsRules.password}
          name="password"
        />
        {errors.password && <ErrorMessage message={t(errors.password!.message! as any)} />}
        {mutation.isError && mutation.error?.states && mutation.error.states.some((x) => x.code === CredentialsRulesErrorsCodes.EmailAddressOrPasswordIsIncorrect) && <ErrorMessage message={t('invalidEmailAddressOrPassword')} />}
      </div>
      <div className="col-12">
        <ButtonSecondary loading={mutation.isLoading} type="submit" label={t('iWantToLogIn')} />
      </div>
    </form>
  );
};

const ObservedLoginForm = observer(LoginForm);
export { ObservedLoginForm as LoginForm };
