/* eslint-disable no-return-await */
/* eslint-disable class-methods-use-this */
import axios, { AxiosRequestConfig, AxiosResponse } from 'axios';
import { HttpClient as IHttpClient, HttpClientRequestParameters } from '../interfaces/http-client';

export class HttpClient implements IHttpClient {
  async get<T, R>(parameters: HttpClientRequestParameters<T>): Promise<R> {
    const { url } = parameters;
    const options: AxiosRequestConfig = {
      headers: {},
    };

    return (await axios.get<R>(url, options)).data;
  }

  async post<T, R>(parameters: HttpClientRequestParameters<T>): Promise<R> {
    const { url, payload } = parameters;
    const options: AxiosRequestConfig = {
      headers: {},
    };
    return await axios.post<T, R>(url, payload, options);
  }

  async getFile(parameters: HttpClientRequestParameters<void>): Promise<void> {
    const { url } = parameters;

    const options: AxiosRequestConfig = {
      headers: {},
      responseType: 'blob',
    };

    return axios.get(url, options)
      .then((response) => {
        const type = response.headers['content-type'];
        const fileName = HttpClient.getFileName(response)!;
        const blob = new Blob([response.data], { type });
        const link = document.createElement('a');
        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
      });
  }

  private static getFileName(response: AxiosResponse) {
    const disposition = response.headers['content-disposition'];
    if (disposition && disposition.indexOf('attachment') !== -1) {
      const filenameRegex = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/;
      const matches = filenameRegex.exec(disposition);
      if (matches != null && matches[1]) {
        return matches[1].replace(/['"]/g, '');
      }
    }

    return undefined;
  }
}

const httpClient = new HttpClient();

export { httpClient };
