import React from 'react';
import { iconsMap } from 'ui/utils';
import { Icon } from '../icon/icon';

interface AppIconProps {
  usage: keyof typeof iconsMap;
  className?: string;
}

/** Wykorzystuje predefiniowane ikony */
const AppIcon = (props: AppIconProps) => {
  const { usage, className } = props;
  return <Icon className={`${iconsMap[usage]} ${className}`} />;
};

export { AppIcon };
