import { IValueObject } from '../base/index';
import { IClaim } from './claim';
import { IRole } from './role';

export interface IUserPermissions extends IValueObject {
    roles: Array<IRole>;

    claims: Array<IClaim>;
}

export class UserPermissions implements IUserPermissions {
  roles: Array<IRole>;

  claims: Array<IClaim>;

  constructor(userPermissions: IUserPermissions) {
    this.roles = userPermissions.roles;
    this.claims = userPermissions.claims;
  }

  hasClaim = (claim: string): boolean => this.roles
    .flatMap((r) => r.claims)
    .concat(this.claims)
    .map((r) => r.value)
    .some((r) => r === claim);
}
