/* eslint-disable react/destructuring-assignment */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable max-len */
/* eslint-disable react/jsx-props-no-spreading */
import React, {
  useCallback, useContext, useEffect, useRef,
} from 'react';
import { FormInputText } from 'ui/components/molecues';
import { FormProvider, useForm, Validate } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import {
  AnimatedDiv, AppIcon, Divider,
} from 'ui/components/atoms';
import { StaffDataValidationRules } from 'domain/staff/staff-data-rules';
import { StaffData } from 'domain/staff/staff-data';
import { observer } from 'mobx-react';
import { AddressRegistrationFields } from '../../address-registration-fields/address-registration-fields';
import { RegisterFormStore } from '../register-form-store';
import { FormStepProps } from '../form-step-props';

// eslint-disable-next-line max-len
const StaffRegistrationForm = (props: FormStepProps) => {
  const store = useContext(RegisterFormStore);

  const staffRegistrationForm = useForm<StaffData>({
    defaultValues: store.staffData,
    reValidateMode: 'onChange',
  });

  const {
    control, getValues, formState, handleSubmit,
  } = staffRegistrationForm;

  const form = useRef<HTMLFormElement | null>(null);

  useEffect(() => {
    store.isCurrentStepValid = formState.isValid;
  }, [formState.isValid]);

  useEffect(() => {
    if (store.validateCurrentStep === true) {
      form.current!.dispatchEvent(new Event('submit', { bubbles: true, cancelable: true }));
    }
  }, [store.validateCurrentStep, store.validateTimeStamp]);

  const sendForm = useCallback((e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    handleSubmit(() => {
      store.staffData = getValues();
      store.isCurrentStepValid = undefined;
      store.validateCurrentStep = undefined;
      store.validateTimeStamp = undefined;
      props.onSubmit();
    })(e);
  }, []);

  useEffect(() => () => {
    store.staffData = getValues();
  }, []);

  const { t } = useTranslation();

  return (
    <AnimatedDiv>
      <FormProvider {...staffRegistrationForm}>
        <form ref={form} onSubmit={sendForm} className="formgrid grid">
          <Divider align="center">
            <AppIcon usage="staff" className="pr-2" />
            {t('staffInfo')}
          </Divider>
          <div className="field col-12 md:col-12">
            <FormInputText
              id="name"
              control={control}
              label={t('staffName')}
              rules={StaffDataValidationRules.name as Record<string, Validate<string>>}
              name="name"
              defaultValue=""
            />
          </div>
          <div className="field col-12 md:col-6">
            <FormInputText
              id="phoneNumber"
              control={control}
              label={t('phoneNumber')}
              defaultValue=""
              rules={StaffDataValidationRules.phoneNumber as Record<string, Validate<string>>}
              name="phoneNumber"
            />
          </div>
          <div className="field col-12 md:col-6">
            <FormInputText
              id="emailAddress"
              control={control}
              label={t('emailAddress')}
              defaultValue=""
              rules={StaffDataValidationRules.emailAddress as Record<string, Validate<string>>}
              name="emailAddress"
            />
          </div>
          <Divider align="center">
            <AppIcon usage="address" className="pr-2" />
            {t('staffAddress')}
          </Divider>
          <AddressRegistrationFields name="address" />
          <Divider align="center">
            <AppIcon usage="actionPlan" className="pr-2" />
            {t('actionMessage')}
          </Divider>
          <div className="field col-12 md:col-12">
            <FormInputText
              id="volunteersAmount"
              control={control}
              name="volunteersAmount"
              label={t('volunteersAmount')}
              defaultValue=""
              type="number"
              rules={StaffDataValidationRules.volunteersAmount as Record<string, Validate<string>>}
            />
          </div>
        </form>
      </FormProvider>
    </AnimatedDiv>
  );
};

const ObservedStaffRegistrationForm = observer(StaffRegistrationForm);

export { ObservedStaffRegistrationForm as StaffRegistrationForm };
