/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable max-len */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/jsx-props-no-spreading */
import React, {
  useCallback, useContext, useEffect, useRef,
} from 'react';
import {
  AnimatedDiv, AppIcon, Divider,
} from 'ui/components/atoms';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import {
  FormProvider, useForm, useWatch, Validate,
} from 'react-hook-form';
import { FormCheckbox, FormInputText } from 'ui/components/molecues';
import { CheckboxChangeParams } from 'primereact/checkbox';
import { observer } from 'mobx-react';
import { MaterialsDemand } from 'domain/staff/materials-demand';
import { MaterialsDemandValidationRules } from 'domain/staff/materials-demand-rules';
import { AddressRegistrationFields } from '../../address-registration-fields/address-registration-fields';
import { RegisterFormStore } from '../register-form-store';
import { FormStepProps } from '../form-step-props';

const Div = styled.div`
  padding-top: 0px;
  margin-bottom: 1rem;
`;

// eslint-disable-next-line max-len
const DemandRegistrationForm = (props: FormStepProps) => {
  const store = useContext(RegisterFormStore);

  const materialsDemandRegistrationForm = useForm<MaterialsDemand>({
    defaultValues: store.materialsDemand,
    reValidateMode: 'onChange',
  });

  const {
    control, setValue, getValues, formState, handleSubmit,
  } = materialsDemandRegistrationForm;

  const copyAddressFromStaffAddress = useWatch({
    control,
    name: 'copyAddressFromStaffAddress',
  });

  const form = useRef<HTMLFormElement | null>(null);

  useEffect(() => {
    store.isCurrentStepValid = formState.isValid;
  }, [formState.isValid]);

  useEffect(() => {
    if (store.validateCurrentStep === true) {
      form.current!.dispatchEvent(new Event('submit', { bubbles: true, cancelable: true }));
    }
  }, [store.validateCurrentStep, store.validateTimeStamp]);

  const sendForm = useCallback((e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    handleSubmit(() => {
      store.materialsDemand = getValues();
      store.isCurrentStepValid = undefined;
      store.validateCurrentStep = undefined;
      store.validateTimeStamp = undefined;
      props.onSubmit();
    })(e);
  }, []);

  const { t } = useTranslation();

  useEffect(() => {
    setValue('deliveryAddress', store.staffData.address);
  }, []);

  const onCopyAddressFromStaffAddressChange = useCallback((e: CheckboxChangeParams) => {
    setValue('copyAddressFromStaffAddress', e.checked);
    if (e.checked) {
      setValue('deliveryAddress', store.staffData.address);
    }
  }, []);

  useEffect(() => () => {
    store.materialsDemand = getValues();
  }, []);

  useEffect(() => {
  }, [store.materialsDemand.copyAddressFromStaffAddress]);

  return (
    <AnimatedDiv>
      <FormProvider {...materialsDemandRegistrationForm}>
        <form ref={form} onSubmit={sendForm} className="formgrid grid">
          <Divider align="center">
            <AppIcon usage="demand" className="pr-2" />
            {t('demandMessage')}
          </Divider>
          <div className="field col-12 md:col-4">
            <FormInputText
              id="charityBoxesAmount"
              control={control}
              name="charityBoxesAmount"
              label={t('charityBoxesAmount')}
              defaultValue=""
              rules={MaterialsDemandValidationRules.charityBoxesAmount as Record<string, Validate<string>>}
              type="number"
            />
          </div>
          <div className="field col-12 md:col-4">
            <FormInputText
              id="postersAmount"
              control={control}
              name="postersAmount"
              label={t('postersAmount')}
              defaultValue=""
              rules={MaterialsDemandValidationRules.postersAmount as Record<string, Validate<string>>}
              type="number"
            />
          </div>
          <div className="field col-12 md:col-4">
            <FormInputText
              id="acknowledgementsAmount"
              control={control}
              name="acknowledgementsAmount"
              label={t('acknowledgementsAmount')}
              defaultValue=""
              rules={MaterialsDemandValidationRules.acknowledgementsAmount as Record<string, Validate<string>>}
              type="number"
            />
          </div>
          <Divider align="center">
            <AppIcon usage="delivery" className="pr-2" />
            {t('deliveryAddressMessage')}
          </Divider>
          <Div className="col-12 md:col-12">
            <FormCheckbox
              inputId="copyAddressFromStaffAddress"
              control={control}
              name="copyAddressFromStaffAddress"
              label={[t('deliverToStaffAddress'), t('deliverToOtherAddress')]}
              onChange={onCopyAddressFromStaffAddressChange}
              defaultChecked
            />
          </Div>
          <AddressRegistrationFields name="deliveryAddress" disabled={copyAddressFromStaffAddress} />
        </form>
      </FormProvider>
    </AnimatedDiv>
  );
};

const ObservedDemandRegistrationForm = observer(DemandRegistrationForm);

export { ObservedDemandRegistrationForm as DemandRegistrationForm };
