/* eslint-disable no-console */

import { JL } from 'jsnlog';
import StackTrace from 'stacktrace-js';

export type LoggerOptions = {
  isClientSideLoggingEnabled?: boolean,
  serverSideLogging?: {
    enabled: true,
    loggerName: string,
    url: string,
  } | {
    enabled: false
  }
}

class Logger {
  private static options: LoggerOptions = {
    isClientSideLoggingEnabled: true,
    serverSideLogging: {
      enabled: false,
    },
  };

  public static init = (opt: LoggerOptions) => {
    this.options = opt;

    if (this.options.serverSideLogging?.enabled === true) {
      JL.setOptions({
        defaultAjaxUrl: this.options.serverSideLogging.url,
        enabled: true,
      });
    }
  };

  public static info = (message?: unknown, ...optionalParams: unknown[]): void => {
    if (this.options.isClientSideLoggingEnabled === true) {
      console.info(message, optionalParams);
    }
  };

  public static warn = (message?: unknown, ...optionalParams: unknown[]): void => {
    if (this.options.isClientSideLoggingEnabled === true) {
      console.warn(message, optionalParams);
    }
  };

  public static error = (error: unknown): void => {
    if (this.options.isClientSideLoggingEnabled === true) {
      console.error(error);
    }

    if (this.options.serverSideLogging?.enabled === true) {
      const { loggerName } = this.options.serverSideLogging;

      if (error instanceof Error) {
        StackTrace.fromError(error).then((stackFrames) => {
          const stringifiedStack = stackFrames.map((sf) => sf.toString()).join('\n');

          JL(loggerName).error({
            name: error.name,
            message: error.message,
            cause: error.cause,
            stack: stringifiedStack,
          });
        });
      } else {
        JL(loggerName).error(error);
      }
    }
  };
}

export default Logger;
