import React, { forwardRef } from 'react';
import {
  Toast as PrimereactToast,
  ToastProps as PrimereactToastProps,
  ToastMessage as PrimereactToastMessage,
} from 'primereact/toast';

export type ToastProps = PrimereactToastProps

export type ToastMessage = PrimereactToastMessage;

const Toast = forwardRef(
  (props: ToastProps, ref?: React.LegacyRef<PrimereactToast> | undefined) => (
  // eslint-disable-next-line react/jsx-props-no-spreading
    <PrimereactToast {...props} ref={ref} />
  ),
);

export { Toast };
