/* eslint-disable class-methods-use-this */
import axios from 'axios';
import { IUser, User } from 'domain/identity/user';
import { ErrorsHandler } from 'infrastructure/errors-handling';
import { IUserRepository } from 'domain/identity/abstraction/user-repository';
import { env } from '../../configuration/env';

class UserRepository implements IUserRepository {
  getUser = async (): Promise<User> => {
    try {
      const result = await axios.get<IUser>(`${env.REACT_APP_API_URL}/user/data`);

      return Promise.resolve(new User(result.data));
    } catch (error) {
      ErrorsHandler.handle(error);

      return Promise.reject(error);
    }
  };
}

const userRepository: IUserRepository = new UserRepository();

export { userRepository as UserRepository };
