import { makeAutoObservable } from 'mobx';
import { Address, FullName } from 'domain/value-objects';
import { createContext } from 'react';
import {
  IPlenipotentiary, Plenipotentiary, IMaterialsDemand, MaterialsDemand, IStaffData, StaffData,
} from 'domain/staff';

export interface IRegisterFormService {
    staffData: IStaffData,
    materialsDemand: IMaterialsDemand,
    plenipotentiary: IPlenipotentiary,
    isCurrentStepValid?: boolean,
    validateCurrentStep?: boolean,
    validateTimeStamp?: Date,
    reset: () => void;
}

export class RegisterFormService implements IRegisterFormService {
  staffData!: IStaffData;

  materialsDemand!: IMaterialsDemand;

  plenipotentiary!: IPlenipotentiary;

  isCurrentStepValid?: boolean;

  validateCurrentStep?: boolean;

  validateTimeStamp?: Date;

  /**
     *
     */
  constructor() {
    this.reset();

    makeAutoObservable(this);
  }

  reset = (): void => {
    this.staffData = new StaffData({
      address: new Address({
        city: '',
        number: undefined,
        apartmentNumber: undefined,
        street: '',
        voivodeship: undefined,
        zipCode: '',
      }),
      emailAddress: '',
      name: '',
      phoneNumber: '',
      volunteersAmount: undefined,
    });

    this.materialsDemand = new MaterialsDemand({
      acknowledgementsAmount: undefined,
      copyAddressFromStaffAddress: true,
      deliveryAddress: new Address({
        city: '',
        number: undefined,
        apartmentNumber: undefined,
        street: '',
        voivodeship: undefined,
        zipCode: '',
      }),
      charityBoxesAmount: undefined,
      postersAmount: undefined,
    });

    this.plenipotentiary = new Plenipotentiary({
      address: new Address({
        city: '',
        number: undefined,
        apartmentNumber: undefined,
        street: '',
        voivodeship: undefined,
        zipCode: '',
      }),
      emailAddress: '',
      fullName: new FullName({
        firstName: '',
        lastName: '',
      }),
      pesel: '',
      phoneNumber: '',
      gdprConsent: false,
    });

    this.isCurrentStepValid = undefined;
    this.validateCurrentStep = undefined;
    this.validateTimeStamp = undefined;
  };
}

const RegisterFormStore = createContext<IRegisterFormService>(new RegisterFormService());

export { RegisterFormStore };
