import React from 'react';
import { Routes, Route } from 'react-router';
import { DefaultLayout } from 'ui/components/layouts';
import { HomePage, RegistrationPage } from 'ui/components/pages';

const AnonymousDpRoutes: React.FC = (): JSX.Element => (
  <Routes>
    <Route path="/" element={<DefaultLayout />}>
      <Route index element={<HomePage />} />
      <Route path="rejestracja" element={<RegistrationPage />} />
      <Route path="*" element={<HomePage />} />
    </Route>
  </Routes>
);

export default AnonymousDpRoutes;
