/* eslint-disable operator-linebreak */
import { maximumLengthIs, ValidationMessages } from 'domain/shared-utils';
import { RegisterOptions } from 'react-hook-form';
import { EmailRegexp } from 'shared/regexp/email-regexp';
import { StaffData } from './staff-data';

const StaffDataValidationRules: {
    [index in keyof StaffData]?: RegisterOptions<StaffData, index>
} = {
  name: {
    required: {
      value: true,
      message: ValidationMessages.canNotBeEmpty,
    },
    maxLength: {
      value: 256,
      message: maximumLengthIs(256),
    },
  },
  volunteersAmount: {
    required: {
      value: true,
      message: ValidationMessages.canNotBeEmpty,
    },
    min: {
      value: 1,
      message: ValidationMessages.minimumAmountIsOne,
    },
  },
  emailAddress: {
    required: {
      value: true,
      message: ValidationMessages.canNotBeEmpty,
    },
    maxLength: {
      value: 256,
      message: maximumLengthIs(256),
    },
    pattern: {
      value: new RegExp(EmailRegexp),
      message: ValidationMessages.emailAddressHasIncorrectFormat,
    },
  },
  phoneNumber: {
    required: {
      value: true,
      message: ValidationMessages.canNotBeEmpty,
    },
    maxLength: {
      value: 16,
      message: maximumLengthIs(16),
    },
  },
};

export { StaffDataValidationRules };
