import { Address, IAddress } from 'domain/value-objects';
import { IValueObject } from '../base/index';

export interface IStaffData extends IValueObject {
    name: string;
    volunteersAmount?: number | undefined;
    emailAddress: string;
    phoneNumber: string;
    address: IAddress;
}

export class StaffData implements IStaffData {
  name: string;

  volunteersAmount?: number;

  emailAddress: string;

  phoneNumber: string;

  address: IAddress;

  /**
   *
   */
  constructor(staffData: IStaffData) {
    this.name = staffData.name;
    this.volunteersAmount = staffData.volunteersAmount;
    this.emailAddress = staffData.emailAddress;
    this.phoneNumber = staffData.phoneNumber;
    this.address = new Address(staffData.address);
  }
}
