/* eslint-disable @typescript-eslint/no-explicit-any */
import { StaffRegisterDTO, StaffRepository as IStaffRepository } from 'domain/staff';
import { StaffRegisterMapper } from 'infrastructure/mappers';
import { httpClient, HttpClient } from 'infrastructure/http-client';
import { IStaff } from 'domain/staff/staff';
import { ErrorsHandler } from 'infrastructure/errors-handling';

class StaffRepository implements IStaffRepository<HttpClient> {
  httpClient: HttpClient;

  endpoint: string;

  constructor(httpClient: HttpClient, endpoint: string) {
    this.httpClient = httpClient;
    this.endpoint = endpoint;
  }

  getStaffsReport(): Promise<void> {
    return this.httpClient.getFile({ url: `${this.endpoint}/all/report` });
  }

  async create(newStaff: StaffRegisterDTO) {
    try {
      await this.httpClient.post<StaffRegisterDTO, void>({ url: `${this.endpoint}/register`, payload: newStaff });

      return Promise.resolve();
    } catch (error) {
      ErrorsHandler.handle(error);

      return Promise.reject((error as any).response.data);
    }
  }

  async createNewStaff(staff: IStaff): Promise<void> {
    const staffRegisterDTO = StaffRegisterMapper.toDTO(staff);
    return await this.create(staffRegisterDTO);
  }
}

const staffRepository = new StaffRepository(httpClient, '/Staff');

export { staffRepository };
