/* eslint-disable react/no-unstable-nested-components */
import React, { Suspense, useEffect } from 'react';
import './app.css';
import { Theme } from 'ui/theme';
import { ErrorPage, SuspensePage } from 'ui/components/pages';
import { QueryClient, QueryClientProvider } from 'react-query';
import './i18n';
import { HashRouter } from 'react-router-dom';
import { env } from 'infrastructure/configuration/env';
import { ErrorsHandlingStartup } from 'infrastructure/errors-handling';
import { LoggingStartup } from 'infrastructure/logging';
import { ErrorBoundary } from 'react-error-boundary';
import DpRoutes from 'infrastructure/routing/dp-routes';
import { EmptyLayout } from 'ui/components/layouts';
import ReactGA from 'react-ga';
import { CommunicationWithApiStartup } from './infrastructure/communication-with-api';
import { AuthenticationStartup } from './infrastructure/authentication';

ReactGA.initialize(env.REACT_APP_GA_TRACKING_ID, { testMode: !!process.env.TESTING_MODE });

const queryClient = new QueryClient();

const App = () => {
  useEffect(() => {
    ErrorsHandlingStartup.init();
    LoggingStartup.init();
    CommunicationWithApiStartup.init();
    AuthenticationStartup.init();
  }, []);

  return (
    <ErrorBoundary
      FallbackComponent={(props) => (
        <EmptyLayout>
          <ErrorPage error={props.error} resetErrorBoundary={props.resetErrorBoundary} />
        </EmptyLayout>
      )}
    >
      <Suspense fallback={(<EmptyLayout><SuspensePage /></EmptyLayout>)}>
        <Theme>
          <QueryClientProvider client={queryClient}>
            <HashRouter basename={env.REACT_APP_BASENAME}>
              <DpRoutes />
            </HashRouter>
          </QueryClientProvider>
        </Theme>
      </Suspense>
    </ErrorBoundary>
  );
};

export { App };
