import { AuthenticationService } from 'infrastructure/authentication';
import { createContext } from 'react';
import { makeAutoObservable } from 'mobx';

export enum AuthenticationStatus {
    Authenticated = 1,
    NotAuthenticated = 2
}

export interface IAuthService {
    readonly isAuthenticated: boolean;

    updateAuthenticationStatus: (status: AuthenticationStatus) => void;
}

class AuthService implements IAuthService {
  isAuthenticated: boolean;

  constructor() {
    this.isAuthenticated = AuthenticationService.isAuthenticated;
    makeAutoObservable(this);
  }

  updateAuthenticationStatus = (status: AuthenticationStatus): void => {
    this.isAuthenticated = status === AuthenticationStatus.Authenticated;
  };
}

const AuthStore = createContext<IAuthService>(new AuthService());

export { AuthStore };
