/* eslint-disable max-len */
/* eslint-disable react/jsx-props-no-spreading */
import React, {
  useCallback, useContext, useEffect, useMemo, useRef, useState,
} from 'react';
import styled from 'styled-components';
import {
  Card, Steps, ButtonSuccess, ButtonSecondary, AnimatedDiv, Toast, createErrorMessage,
} from 'ui/components/atoms';
import { useTranslation } from 'react-i18next';
import { devices, deviceSize } from 'ui/utils';
import { useMutation } from 'react-query';
import { staffRepository } from 'infrastructure/domain/staff';
import { ConfirmDialogStatuses } from 'ui/components/organisms';
import { BusinessValidationExceptionResultModel } from 'infrastructure/api-models/business-validation-exception-result-model';
import { IStaff } from 'domain/staff';
import { observer } from 'mobx-react';
import { useAnalyticsEventTracker } from 'ui/utils/hooks';
import {
  StaffRegistrationForm,
} from './staff-registration-form/staff-registration-form';
import {
  PlenipotentiaryRegistrationForm,
} from './plenipotentiary-registration-form/plenipotentiary-registration-form';
import { DemandRegistrationForm } from './demand-registration-form/demand-registration-form';
import { RegistrationConfirmation } from './registration-confirmation/registration-confirmation';
import { RegisterFormService, RegisterFormStore } from './register-form-store';

const StyledSteps = styled(Steps)`
  margin-bottom: 2rem;
  .p-steps-item {
    width: 33.333%;
  }
`;

const Div = styled.div`
  max-height: 80%;
  @media ${devices.laptop} {
    min-height: 37.3em;
  }
  overflow-y: auto;
`;

enum registrationStep {
  staff = 0,
  delivery = 1,
  plenipotentiary = 2,
}

export type RegistrationFormInput = IStaff & {
  staffData: {
    materialsDemand: {
      copyAddressFromStaffAddress: boolean
    }
  },
  plenipotentiary: {
    gdprConsent: boolean,
  }
}

interface RegistrationFormProps {
  className?: string;
}

const ResponsiveDiv = styled.div`
  margin-right: 2vw;
  margin-left: 2vw;
  @media screen and (min-width: ${deviceSize.tablet}px) {
    max-width: 800px;
    margin-right: unset;
    margin-left: unset;
  }
`;

const RegistrationForm = (props: RegistrationFormProps) => {
  const gaEventTracker = useAnalyticsEventTracker('Registration form');
  const store = useContext(RegisterFormStore);

  const { className } = props;
  const [currentStep, setCurrentStep] = useState<registrationStep>(
    registrationStep.staff,
  );
  const { t } = useTranslation();

  const [
    isRegistrationConfirmationVisible,
    setIsRegistrationConfirmationVisible,
  ] = useState(false);

  const [currentStatus, setCurrentStatus] = useState<ConfirmDialogStatuses>(
    ConfirmDialogStatuses.confirmation,
  );

  /** @todo pozbyć się tego any */
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const toast = useRef<any>(null);

  const mutation = useMutation<
    void,
    BusinessValidationExceptionResultModel,
    IStaff
  >(
    (args: IStaff) => staffRepository.createNewStaff({ ...args }),
  );

  useEffect(() => {
    if (mutation.isError) {
      if (mutation.error?.states) {
        setIsRegistrationConfirmationVisible(false);
        setCurrentStatus(ConfirmDialogStatuses.confirmation);
        toast.current?.show(
          mutation.error?.states.map((error) => createErrorMessage(t('error'), error.value)),
        );
      }
    }
  }, [mutation.isError]);

  useEffect(() => {
    if (mutation.isSuccess) {
      store.reset();
      setCurrentStep(registrationStep.staff);
      setCurrentStatus(ConfirmDialogStatuses.success);
    }
  }, [mutation.isSuccess]);

  useEffect(() => {
    if (mutation.isLoading) {
      setCurrentStatus(ConfirmDialogStatuses.loading);
    }
  }, [mutation.status]);

  const goBack = useCallback(() => {
    gaEventTracker('go_back', 'User going backwards in registration form');
    store.isCurrentStepValid = undefined;
    store.validateCurrentStep = undefined;
    store.validateTimeStamp = undefined;
    setCurrentStep(Math.max(registrationStep.staff, currentStep - 1));
  }, [currentStep]);

  const goForward = useCallback(() => {
    gaEventTracker('go_forward', 'User going forward in registration form');
    store.validateCurrentStep = true;
    store.validateTimeStamp = new Date();
  }, [currentStep]);

  const onAccepted = useCallback(() => {
    gaEventTracker('submit_form', 'User submitting registration form');
    mutation.mutate({ ...store });
  }, []);

  const onRejected = useCallback(() => {
    gaEventTracker('not_confirmed', 'User resigned at confirmation');
    setIsRegistrationConfirmationVisible(false);
  }, []);

  const steps = useMemo(() => [
    { label: t('staff') },
    { label: t('materials') },
    { label: t('plenipotentiary') },
  ], []);

  const renderStep = useCallback(
    (currentStep: registrationStep) => {
      switch (currentStep) {
        case registrationStep.staff:
          return (
            <StaffRegistrationForm onSubmit={() => setCurrentStep(Math.min(registrationStep.plenipotentiary, currentStep + 1))} />
          );
        case registrationStep.delivery:
          return (
            <DemandRegistrationForm onSubmit={() => setCurrentStep(Math.min(registrationStep.plenipotentiary, currentStep + 1))} />
          );
        case registrationStep.plenipotentiary:
          return (
            <PlenipotentiaryRegistrationForm onSubmit={() => setIsRegistrationConfirmationVisible(true)} />
          );
        default:
          return (
            <StaffRegistrationForm onSubmit={() => setCurrentStep(Math.min(registrationStep.plenipotentiary, currentStep + 1))} />
          );
      }
    },
    [currentStep],
  );

  const renderButtons = useCallback(
    (currentStep: registrationStep) => {
      switch (currentStep) {
        case registrationStep.plenipotentiary:
          return (
            <>
              <div id="goback" className="col-6 md:col-offset-6 md:col-3">
                <ButtonSecondary onClick={goBack}>{t('goBack')}</ButtonSecondary>
              </div>
              <div id="register" className="col-6 md:col-3">
                <ButtonSuccess onClick={goForward}>
                  {t('register')}
                </ButtonSuccess>
              </div>
            </>
          );
        case registrationStep.staff:
          return (
            <div className="col-6 col-offset-6 md:col-offset-9 md:col-3">
              <ButtonSuccess onClick={goForward}>{t('goForward')}</ButtonSuccess>
            </div>
          );
        default:
          return (
            <>
              <div className="col-6 md:col-offset-6 md:col-3">
                <ButtonSecondary onClick={goBack}>{t('goBack')}</ButtonSecondary>
              </div>
              <div className="col-6 md:col-3">
                <ButtonSuccess onClick={goForward}>{t('goForward')}</ButtonSuccess>
              </div>
            </>
          );
      }
    },
    [currentStep],
  );

  return (
    <ResponsiveDiv>
      <Toast ref={toast} />
      <AnimatedDiv>
        <Card title={t('registration')} className={className}>
          <StyledSteps
            activeIndex={currentStep}
            model={steps}
            onSelect={(e) => setCurrentStep(e.index)}
            readOnly
          />
          <div className="grid" id="cardbody">
            <Div id="form" className="col-12 md:col-12">
              {renderStep(currentStep)}
              {isRegistrationConfirmationVisible && (
                <RegistrationConfirmation
                  isVisible={isRegistrationConfirmationVisible}
                  onAccepted={onAccepted}
                  onRejected={onRejected}
                  status={currentStatus}
                />
              )}
            </Div>
          </div>
          <div className="grid">
            {renderButtons(currentStep)}
          </div>
        </Card>
      </AnimatedDiv>
    </ResponsiveDiv>
  );
};

const ObservedRegistrationForm = observer(RegistrationForm);

const StandaloneRegistrationForm = () => {
  const registerFormService = useMemo(() => new RegisterFormService(), []);
  return (
    <RegisterFormStore.Provider value={registerFormService}>
      <ObservedRegistrationForm />
    </RegisterFormStore.Provider>
  );
};

export { StandaloneRegistrationForm as RegistrationForm };
